import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { delegateCombine, delegateRemove } from 'igniteui-core/type';
import { ToolContextBindingMode, ToolContextBindingMode_$type } from "igniteui-core/ToolContextBindingMode";
import { IgcPropertyUpdatedEventArgs } from "igniteui-core/igc-property-updated-event-args";
import { ToolContextBinding as ToolContextBinding_internal } from "./ToolContextBinding";
import { ensureEnum } from "igniteui-core/componentUtil";

export class IgcToolContextBinding
{

protected createImplementation() : ToolContextBinding_internal
{
	return new ToolContextBinding_internal();
}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : ToolContextBinding_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	get bindingMode() : ToolContextBindingMode {
		return this.i.d;
	}
	set bindingMode(v: ToolContextBindingMode) {
		this.i.d = ensureEnum<ToolContextBindingMode>(ToolContextBindingMode_$type, v);
	}
	get contextKey() : string {
		return (this.i.h as string);
	}
	set contextKey(v: string) {
		this.i.h = v;
	}
	get propertyName() : string {
		return (this.i.j as string);
	}
	set propertyName(v: string) {
		this.i.j = v;
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	
	        return null;
	    }
	
	    private _propertyUpdated: (s: IgcToolContextBinding, e: IgcPropertyUpdatedEventArgs) => void = null;
	    private _propertyUpdated_wrapped: any = null;
	
	public get propertyUpdated(): (s: IgcToolContextBinding, e: IgcPropertyUpdatedEventArgs) => void {
	return this._propertyUpdated;
	}
	    public set propertyUpdated(ev: (s: IgcToolContextBinding, e: IgcPropertyUpdatedEventArgs) => void) {
	        if (this._propertyUpdated_wrapped !== null) {
	            this.i.propertyUpdated = delegateRemove(this.i.propertyUpdated, this._propertyUpdated_wrapped);
	            this._propertyUpdated_wrapped = null;
	            this._propertyUpdated = null;
	        }
	
	        this._propertyUpdated = ev;
	        this._propertyUpdated_wrapped = (o, e) => {
	            
	                    let outerArgs = new IgcPropertyUpdatedEventArgs();
	(<any>outerArgs)._provideImplementation(e);
	                    if ((this as any).beforePropertyUpdated) {
	                        (this as any).beforePropertyUpdated(this, outerArgs);
	                    }
	                    if (this._propertyUpdated) {
	    this._propertyUpdated(this, outerArgs);
	                    }
	
	        };
	        this.i.propertyUpdated = delegateCombine(this.i.propertyUpdated, this._propertyUpdated_wrapped);;
	    }
}
