import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { ListPanelPrimaryKeyValue as ListPanelPrimaryKeyValue_internal } from "./ListPanelPrimaryKeyValue";

/**
 * A primary key value.
*/
export class IgcListPanelPrimaryKeyValue
{
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : ListPanelPrimaryKeyValue_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {									
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	/**
	 * The primary key associated with this value.
	*/
	get key() : string[] {
		return this.i.key as string[];
	}
	set key(v: string[]) {
	                                            if (v && !Array.isArray(v) && typeof(v) == "string") {
	                                                const re = /\s*(?:,|\s|$)\s*/gm;
	                                                v = (v as string).split(re) as any as string[];
	                                            }
		    this.i.key = v;
	    }
	/**
	 * The value of the keys for this primary key value.
	*/
	get value() : any[] {
		return this.i.value as any[];
	}
	set value(v: any[]) {
	                                            if (v && !Array.isArray(v) && typeof(v) == "string") {
	                                                const re = /\s*(?:,|\s|$)\s*/gm;
	                                                v = (v as string).split(re) as any as any[];
	                                            }
		    this.i.value = v;
	    }
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	
	        return null;
	    }
	/**
	 * Determines if two primary key values are equal.
	
	* @param other  * The other key value to check against.
	*/
	public equals(other: any) : boolean {
	                        
		let iv = this.i.equals(other);
	                        return (iv);
	}
}
