import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { ListPanelActiveRowChangedEventArgs as ListPanelActiveRowChangedEventArgs_internal } from "./ListPanelActiveRowChangedEventArgs";

/**
 * Information about the change to the selected cells in the grid.
*/
export class IgcListPanelActiveRowChangedEventArgs
{

protected createImplementation() : ListPanelActiveRowChangedEventArgs_internal
{
	return new ListPanelActiveRowChangedEventArgs_internal();
}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : ListPanelActiveRowChangedEventArgs_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	/**
	 * The new active row.
	*/
	get newActiveRow() : number {
		return (this.i.newActiveRow as number);
	}
	set newActiveRow(v: number) {
		this.i.newActiveRow = +v;
	}
	/**
	 * The old active row.
	*/
	get oldActiveRow() : number {
		return (this.i.oldActiveRow as number);
	}
	set oldActiveRow(v: number) {
		this.i.oldActiveRow = +v;
	}
}
